.profile {
  width: 100%;
  display: flex;
  flex-direction: row;
  margin: auto;
  height: auto;
  position: relative;
}

@media screen and (min-width: 320px) and (max-width: 825px) {
  .profile {
    max-width: 88%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
